import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './flipper.css';

class Flipper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFlipping: false,
      flipType: 'down',
      frontTextFromData: 0,
      backTextFromData: 1,
    };
  }

  render() {
    const { isFlipping, flipType, frontTextFromData, backTextFromData } =
      this.state;
    return (
      <div
        className={['M-Flipper', flipType, isFlipping ? 'go' : null].join(' ')}
      >
        <div
          className={'digital front ' + this._textClass(frontTextFromData)}
        ></div>
        <div
          className={'digital back ' + this._textClass(backTextFromData)}
        ></div>
      </div>
    );
  }

  // componentDidMount() {
  //     this.props.onRef(this)
  // }
  test() {
    console.log('test');
  }
  _textClass(number) {
    return 'number' + number;
  }
  _flip(type, front, back) {
    // 
    if (this.isFlipping) {
      return false;
    }
    this.setState({
      frontTextFromData: front,
      backTextFromData: back,
      // type
      flipType: type,
      // true
      isFlipping: true,
    });
    setTimeout(() => {
      this.setState({
        frontTextFromData: back,
        isFlipping: false,
      });
    }, this.props.duration);
  }
  // 
  flipDown(front, back) {
    this._flip('down', front, back);
  }
  // 
  flipUp(front, back) {
    this._flip('up', front, back);
  }
  // 
  setFront(text) {
    this.setState({
      frontTextFromData: text,
    });
  }
  // 
  setBack(text) {
    this.setState({
      backTextFromData: text,
    });
  }
}

// props
Flipper.propTypes = {
  frontText: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  backText: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  duration: PropTypes.number,
};

// props
Flipper.defaultProps = {
  // front paper text
  // 
  frontText: 0,
  // back paper text
  // 
  backText: 1,
  // flipping duration, please be consistent with the CSS animation-duration value.
  // CSSanimation-duration
  duration: 600,
};

export default Flipper;
